.transition {
    &__down {
        opacity: 0.01;
        transform: translateY(-20px);
        transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
        transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    }

    &__up {
        opacity: 0.01;
        transform: translateY(20px);
        transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
        transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    }

    &__fade {
        opacity: 0.01;
        transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    }

    &__done {
        opacity: 1;
        transform: translateY(0px);
        transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1),
        transform 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
    }
}